.App {
  padding: 25px;
  height: 100vh;
}

h1,
h2,
h3,
h4,
h5,
button {
  letter-spacing: 0.1em;
}

/*-- home page --*/
.home-page {
  display: flex;
  height: 100%;
}

/*- nav bar -*/
.home-nav {
  width: 200px;
  text-align: center;
  height: calc(100vh - 50px );
  overflow-y: auto;
  background-color: #EDFAFF;
  margin-right: 25px;
  box-shadow: gray 0px 0px 15px -5px;
}
/* width */
.home-nav::-webkit-scrollbar {
  width: 15px;
}
/* Track */
.home-nav::-webkit-scrollbar-track {
  background: #efefef; 
  margin: 10px;
  border-radius: 10px;
}
/* Handle */
.home-nav::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #555; 
}
/* Handle on hover */
.home-nav::-webkit-scrollbar-thumb:hover {
  background: gray; 
}
.nav-btns {
  margin-top: 50px;
}
.nav-btns div {
  width: 100px;
  border: 2px dashed lightgray;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 5px;
  margin-bottom: 25px;
}
.nav-btns div img {
  width: 30px;
}
.nav-btns div button {
  width: 100%;
  font-size: 1em;
  padding: 5px;
  margin-top: 5px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -5px, rgba(0, 0, 0, 0.04) 0px 5px 10px -5px;
  background-color: #fff;
  border: 0;
  cursor: pointer;
  border-radius: 3px;
  font-weight: bold;
}
.nav-btns div button:hover {
  color: #fff;
  background-color: #537cdc;
  transition: 0.2s;
}
.nav-m-l {
  border: 2px dashed lightgray;
  display: inline-block;
  padding: 25px;
}
.nav-m-l div {
  width: 50px;
  height: 50px;
  margin: auto;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -5px, rgba(0, 0, 0, 0.04) 0px 5px 10px -5px;
  border: 2px solid transparent;
}
.nav-m-l div:hover {
  border: 2px solid #537cdc;
  transition: 0.2s;
}
.nav-m-l .alert,
.nav-m-l .restart-btn {
  padding: 7px;
  position: relative;
  background-color: #fff;
}
.nav-m-l .manager-btn {
  margin: 25px 0;
}
.alert-i {
  position: absolute;
  top: 0;
  right: 0;
  max-width: 12px;
  max-height: 12px;
  border-radius: 50%;
  background-color: red;
}
.nav-m-l div img {
  width: 100%
}


/*- main area -*/
.home-main {
  width: auto;
  flex-grow: 1;
  background-color: #fff;
  border-radius: 25px;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.home-main header {
  display: flex;
  align-items: center;
}
.orders-search {
  border: 1px solid currentColor;
  width: 35px;
  height: 35px;
  margin-right: 25px; 
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: rotate(100deg);
  font-size: 1.5em;
  color: gray;
  cursor: pointer;
}
.orders-search:hover {
  color: #537cdc;
}
.currentemployees {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin: 25px auto;
  border: 2px dashed lightgray;
  padding: 5px;
  justify-content: center;
  border-radius: 25px;
}
.currentemployees p {
  margin: 0 5px;
}
.orders {
  min-height: 400px;
  flex-grow: 1;
}
.operations {
  height: 50%;
  margin: 0 10px;
}
.operations section {
  height: 100%;
  padding: 10px;
  overflow-y: auto;  
  box-shadow: rgba(239, 239, 239,0.8) 0px 10px 10px inset,rgba(239, 239, 239,0.8) 0px -10px 10px inset;  
}
.operations:first-child {
  padding-bottom: 10px;
}
.operations:last-child {
  padding-top: 10px;
}
/* width */
.operations section::-webkit-scrollbar {
  width: 15px;
}
/* Track */
.operations section::-webkit-scrollbar-track {
  background: #efefef; 
  margin: 10px;
  border-radius: 10px;
}
/* Handle */
.operations section::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: lightgray; 
}
/* Handle on hover */
.operations section::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.order-con {
  display: flex;
  align-items: center;
}
.previous {
  white-space: nowrap;
}
.order {
  width: 100%;
  border: 2px solid #efefef;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  border-radius: 5px;
}
.order .ope {
  display: flex;
  align-items: center;
}
.copy ,
.del,
.finish,
.recreate {  
  width: 30px;
  height: 30px;
  border: 1px solid gray;
  margin-right: 10px;
  border-radius: 5px;
  cursor: pointer;
}
.order .ope img {
  width: 100%;
}
.finish:hover {
  background-color: #EDFAFF;
  transition: 0.2s;
}
.finish {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  color: #6BDDDD;
}
.copy:hover {
  background-color: #6BDDDD;
  transition: 0.2s;
}
.del:hover {
  background-color: red;
  transition: 0.2s;
}
.order .value {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.phone-num ,
.order-text {
  background-color: rgba(239, 239, 239, 0.5);
  padding: 5px;
  border-radius: 5px;
}
.order-text{
  width: 400px;
  word-wrap: break-word;
}
.phone-num {
  margin: 0 10px;
}
.order-time {
  color: darkgray;
  margin-left: 5px;
  width: 55px;
}
.choose-em {
  padding: 5px;
  font-size: 1em;
  cursor: pointer;
  border-radius: 5px;
  background-color: #fff;
}
.choose-em:hover {
  background-color: #EDFAFF;
}
.recreate {
  margin: 0 0 0 10px;
  padding: 5px;
}
.recreate:hover {
  background: lightgray;
  transition: 0.2s;
}
.order-counter {
  margin-right: 10px;
  padding: 5px;
  border-radius: 20px;
  min-width: 30px;
  text-align: center;
  font-weight: bold;
  color: #fff;
}
.order.pending .finish,
.order.pending .order-counter,
.order.end .finish {
  display: none;
}
.order.running .order-counter {
  background: #252525;
}
.order.fulfilled .value,
.order.end .value {
  opacity: 0.3;
  user-select: none;
  pointer-events: none;
}
.order.fulfilled .finish {
  border: 1px solid #6BDDDD;
}
.order.fulfilled .copy,
.order.fulfilled .del,
.order.end .copy,
.order.end .del,
.order.end .recreate {
  visibility: hidden;
  user-select: none;
  pointer-events: none;
}
.order.fulfilled .order-counter,
.order.end .order-counter {
  background: gray;
}
.order.warning {
  border: 2px solid red;
}
.order.warning .order-counter {
  background: red;
}
.price {
  width: 60px;
  border: 1px solid;
  margin-right: 10px;
}

/* -- orders search area --*/
.orders-search-container {
  margin-top: 25px;
}
.orders-search-container input {
  font-size: 1em;
  padding: 5px;
  border-radius: 5px;
}
.orders-search-container .order .value {
  opacity: 1;
}


.operations hr {
  border: 2px solid lightgray;
}
.operations-inputs {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.operations-inputs input,
.operations-inputs textarea {
  padding: 5px;
  height: 40px;
  font-size: 1em;
  border-radius: 5px;
  border: 1px solid;
  outline: 0;
}
.operations-inputs input:focus,
.operations-inputs textarea:focus {
  border: 2px solid #537cdc;
  transition: 0.1s;
}
.operations-inputs input {
  width: 150px;
}
.operations-inputs textarea {
  width: 250px;
  margin: 0 10px;
  resize: none;
  transition: 0.2s linear;
}
.operations-inputs textarea:focus {
  height: 80px;
}
.operations-inputs button {
  font-size: 1.5em;
  height: 35px;
  width: 35px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #537cdc;
  border-radius: 2px;
  color: #fff;
  font-weight: bold;
  border: 0;
  cursor: pointer;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em , rgba(90, 125, 188, 0.05) 0px 0.25em 1em ;
}
.operations-inputs button:hover {
  opacity: 0.5;
  transition: 0.2s;
}
.operations-inputs .buttons {
  display: flex;
}
.operations-inputs .buttons .remove {
  background-color: lightgray;
  color: #252525;
  margin-left: 10px;
}

/*-- start-new-day --*/
.start-new-day input {
  padding: 5px;
  font-size: 1em;
}

.nav-res {
  position: absolute;
  left: -200px;
}
.nav-res.open {
  left: 0;
  transition: 0.5s;
}
.nav-res.open .nav-res-btn img {
  transform: rotate(180deg);
  transition: 0.5s;
}
.nav-res-btn {
  padding: 5px;
  position: absolute;
  right: -25px;
  top: 0px;
  width: 50px;
  height: 100%;
  border-radius: 0% 100% 100% 0% / 30% 50% 50% 70% ;
  background-image: linear-gradient(to right , lightgray, transparent, transparent);
  cursor: pointer;
  display: flex;
  align-items: center;
}
.nav-res-btn img {
  width: 100%;
}
.nav-res-btn:hover {
  background-image: linear-gradient(to right , gray, transparent);
  transition: 0.2s;
}
