.admin-page {
  position: relative;
  text-align: center;
}
.admin-page-links {
  display: flex;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -5px, rgba(0, 0, 0, 0.04) 0px 5px 10px -5px;
  background-color: #fff;
  border-radius: 5px;
  position: fixed;
  top: 25px;
  left: 25px;
  padding: 5px;
  z-index: 5;
  border: 1px solid lightgray;
}
.admin-page-links a {
  text-decoration: none;
  color: gray;
  font-size: 1.5em;
}
.admin-page-links p {
  font-size: 1em;
  margin: 0 5px;
  color: gray;
}
.admin-page-links button {
  background-color: inherit;
  border: 0;
  outline: 0;
  font-size: 1em;
  color: gray;
  font-weight: bold;
}
.admin-page-links a:hover,
.admin-page-links button:hover {
  transition: 0.2s;
  color: #537cdc;
  cursor: pointer;
}

.manager-photo {
  width: 100px;
  height: 100px;
}
.admin-page .title {
  color: gray;
  margin: 10px 0;
}
.login-form {
  padding: 25px;
  border: 2px dashed gray;
  display: inline-block;
  background-color: #fff;
  margin-top: 25px;
}
.login-form h2 {
  font-weight: normal;
}
.login-form input {
  border: 1px solid gray;
  margin-top: 10px;
}
.admin-home-links {
  border: 2px groove lightgray;
  display: inline-block;
  width: 400px;
  padding: 25px;
  background-color: #fff;
}
.admin-home-links div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
  padding: 10px 0;
  font-size: 1.1em;
}
.admin-home-links button {
  padding: 5px;
  font-size: 1.1em;
  border: 0;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -5px, rgba(0, 0, 0, 0.04) 0px 5px 10px -5px;
  background-color: #efefef;
  cursor: pointer;
  display: block;
  /* font-weight: bold; */
}
.admin-home-links button:hover {
  background-color: #537cdc;
  color: #fff;
  transition: 0.2s;
}

/*-- controls --*/
.controls-page,
.employees-page {
  border: 1px groove lightgray;
  padding: 25px;
  background-color: #fff;
  max-width: 800px;
  margin: auto;
}
.employees-page { 
  overflow-x: auto;
  max-height: 500px;
}
.controls-page .con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 25px 0;
  border: 1px dashed lightgray;
  padding: 10px;

}
.controls-page .con form .nums ,
.controls-page .con form button {
  max-width: 100px;
  margin: 0 0px 0 10px;
  padding: 5px;
  cursor: pointer;
}
.controls-page .con form button {
  border: 0;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -5px, rgba(0, 0, 0, 0.04) 0px 5px 10px -5px;
  background-color: #537cdc;
  color: #fff;
  font-weight: bold;
}
.controls-page .con form button:hover {
  transition: 0.2s;
  background-color: lightgray;
  color: #000;
}
.controls-page .em {
  align-items: flex-start;
}
.ev-con {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
}
.ev-con h3 {
  margin-bottom: 10px;
}
.ev-con .data-value {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  width: 100%;
}
.ev-con .data-value .remove-toggle {
  width: 25px;
  height: 25px;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightgray;
  font-weight: bold;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -5px, rgba(0, 0, 0, 0.04) 0px 5px 10px -5px;
  cursor: pointer;
}
.ev-con .data-value .remove-toggle:hover {
  transition: 0.2s;
  background-color: #537cdc;
  color: #fff;
}
.ev-con input {
  border: 1px solid lightgray;
  padding: 5px 10px;
  display: inine-block;
  margin-bottom: 10px;
  width: 80%;
  font-weight: bold;
  letter-spacing: 0.1em;
  font-size: 0.9em;
}
.ev-con button {
  margin: 0 !important;
  display: block;
}
.ev-con .add-new {
  border-top: 2px dotted lightgray;
  padding-top: 10px;
}
.ev-con .add-new input {
  font-weight: normal;
  font-size: 0.8em;
}
.data-value.delete input {
  background-color: lightgray;
}

/*-- employees-page --*/
.table-data {
  width: 100%;
  max-height: 400px;
  overflow-x: auto;
  margin: 25px auto 0 auto;
}
.employees-page table {
  text-align: left;
  border-collapse: collapse;
  margin: auto;
}
.employees-page table thead{
  position: sticky;
  top: 0px;
  background-color: #eeeeee;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -5px, rgba(0, 0, 0, 0.04) 0px 5px 10px -5px;
}
.employees-page table tfoot {
  position: sticky;
  bottom: 0px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px -10px 15px -5px, rgba(0, 0, 0, 0.04) 0px -5px 10px -5px;
  background-color: #eeeeee;
  color: #537cdc;
}
.employees-page table tr:nth-child(even) {
  background-color: rgba(211, 211, 211, 0.1);
}
.employees-page table th {
  color: gray;
}
.employees-page table th, 
.employees-page table td {
  border: 1px solid lightgray;
  padding: 10px;
  white-space:nowrap
}
.employees-page table td {
  text-align: center;
}
.employees-page table td input {
  width: 70px;
  padding: 5px;
  font-size: 1em;
}
.employees-page table button {
  background-color: lightgray;
  border: 0;
  padding: 5px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -5px, rgba(0, 0, 0, 0.04) 0px 5px 10px -5px;
  cursor: pointer;
  font-weight: bold;
}
.employees-page table button:hover {
  background-color: #537cdc;
  color: #fff
}
.employees-page tfoot {
  font-weight: bold;
}

/*-- search page --*/
.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px dashed;
  padding: 10px;
  margin-top: 25px;
  position: sticky;
  top: -25px;
  z-index: 1;
  background-color: #fff;
}
.search-bar .massage-error {
  border: 2px dashed red;
  position: absolute;
  bottom: 100%;
  padding: 5px;
  background-color: #fff;
}
.search-bar input {
  margin: 0 10px;
  padding: 5px;
}
.search-bar button {
  background-color: #000;
  color: #fff;
  font-weight: bold;
  border: 0;
  padding: 5px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -5px, rgba(0, 0, 0, 0.04) 0px 5px 10px -5px;
  cursor: pointer;
}
.search-bar button:hover {
  transition: 0.2s;
  background-color: #537cdc;
}

/*-- clients page --*/
.clients-page table {
  margin: auto;
  text-align: center;
}
.clients-page table input {
  width: 120px !important;
}
.clients-page table textarea {
  resize: vertical;
  min-height: 60px;
  padding: 5px;
}
.add-client textarea {
  padding: 5px;
  margin-right: 10px;
}