/* -- fixed container components style -- */
.fixed-container {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.8);
  z-index: 2;
  width: 100vw;
  height: 100vh;
}
.fixed-con {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  outline: 2px dotted lightgray;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  outline-offset: 8px;
  background-color: #fff;
  padding: 25px;
  border-radius: 5px;
  width: 350px;  
  text-align: center;
  max-height: 100vh;
  overflow-y: auto;
}
.fixed-con-search {
  width: auto;
}
/* -- add empoyers --*/
.fixed-con h3 {
  margin-bottom: 20px;
}
.form-layer{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #efefef;
  padding: 5px;
  border-radius: 5px;
  margin: 10px 0;
}
.form-layer label {
  color: gray;
  font-weight: bold;
}
.fixed-con button {
  padding: 5px 10px;
  font-size: 1em;
  font-weight: bold;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 1px 5px;
  background-color: #efefef;
  border: 0;
  cursor: pointer;
}
.fixed-con button:hover {
  opacity: 0.6;
  transition: 0.2s;
}
.fixed-con .close {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 5px;
  left: 5px;
  border: 2px solid;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.fixed-con .close img {
  width: 120%;
}
.fixed-con .close:hover {
  opacity: 0.3;
  transition: 0.2s;
}
.error-massage {
  border: 2px dashed red;
  padding: 5px;
  font-weight: bold;
}
.price-value p:last-child {
  color: #537cdc;
  font-weight: bold;
}
.employer-data input {
  width: 100px;
  border: 1px solid
}
.form-title {
  color: gray;
}
.buttons-con button {
  width: 180px;
  margin: 10px auto;
}

/* handle notes */
.notes {
  max-height: 300px;
  overflow-y: auto;
  margin-top: 10px;
}
/* width */
.notes::-webkit-scrollbar {
  width: 15px;
}
/* Track */
.notes::-webkit-scrollbar-track {
  background: #efefef; 
  margin: 10px;
  border-radius: 10px;
}
/* Handle */
.notes::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: lightgray; 
}
/* Handle on hover */
.notes::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
.note {
  border: 1px solid lightgray;
  padding: 10px;
  margin: 10px 10px 0px 0px;
  text-align: left;
  letter-spacing: 0.1em;
  line-height: 1.2em;
  border-radius: 5px;
  position: relative;
}
.note-date {
  color: darkgray;
  margin-top: 10px;
}
.note button {
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 1.5em;
  padding: 0px 3px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: none;
  border: 1px solid;
  opacity: 0.3;
}
.note button:hover {
  opacity: 1;
}
.fixed-con .manage-notes {
  left: calc(100% - 35px);
  font-size: 1.5em;
}
.notes textarea {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border: 2px dashed;
  resize: vertical;
}